<template>
  <div class="detail" id="notebook-list">
    <header>
      <a href="#" class="btn" @click.prevent="onCreate"><i class="iconfont icon-plus"></i> <span>新建笔记本</span></a>
    </header>
    <main>
      <div class="layout">
        <h3>笔记本列表({{ notebooks.length }})</h3>
        <div class="book-list">
          <router-link v-for="notebook in notebooks" :to="`/note?notebookId=${notebook.id}`" class="notebook" :key="notebook.id">
            <div>
              <span class="iconfont icon-notebook"></span>
              <span class="title">{{ notebook.title }}</span>
              <span class="count">{{ notebook.noteCounts }}</span>
              <span class="action" @click.stop.prevent="onEdit(notebook)">编辑</span>
              <span class="action" @click.stop.prevent="onDelete(notebook)">删除</span>
              <span class="date">{{ notebook.createdAtFriendly }}</span>
            </div>
          </router-link>
        </div>
      </div>

    </main>

  </div>
</template>

<script>



import {mapActions,mapGetters} from 'vuex'

export default {
  data() {
    return {
    }
  },
  created() {
    this.checkLogin({path: '/login'})
    this.getNotebooks()
  },
  computed:{
    ...mapGetters(['notebooks'])
  },
  methods: {
    ...mapActions(['getNotebooks','addNotebook','updateNotebook','deleteNotebook','checkLogin']),
    onEdit(notebook) {

      this.$prompt('请输入新笔记本的标题', '修改标题', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputPattern: /^.{1,30}$/,
        inputValue:notebook.title,
        inputErrorMessage: '标题不能为空，且不超过30个字符'
      }).then(({value}) => {
        this.updateNotebook({notebookId:notebook.id,title:value})

      })

    },
    onDelete(notebook) {
      this.$confirm('确认要删除笔记本吗', '删除笔记本', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteNotebook({notebookId:notebook.id})

      })


    },
    onCreate() {
      this.$prompt('请输入新笔记本的标题', '创建笔记本', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputPattern: /^.{1,30}$/,
        inputErrorMessage: '标题不能为空，且不超过30个字符'
      }).then(({value}) => {
        this.addNotebook({title:value})
      })


    }
  }
}
</script>

<style lang="less" scoped>
@import url(../assets/css/notebookList.less);
</style>



